<template>
  <div id="container">

    <div id="take-part" :class="$store.state.premium === '' ? ' premium' : null">
      <div class="tp-bg"></div>
      <div class="tp-title" v-if="$store.state.lang === 'ru'">Покупка тикетов</div>
      <div class="tp-title" v-else>Ticketlar sotib olish</div>
      <div class="tp-pic">
        <img src="../files/participation.svg" alt="Take Part">
      </div>
      <div class="tp-desc" v-if="$store.state.lang === 'ru'">В своём стремлении<br>улучшить пользовательский..</div>
      <div class="tp-desc" v-else>O'z harakatingizbilan<br>Imkoniyatni oshiring</div>
      <div class="tp-form">
        <div class="btn tp-ticket-rm" id="removeTicket" @click.prevent="ticketCount < 3 ? 2 : ticketCount--">
          <ion-icon name="remove"></ion-icon>
        </div>
        <div class="tp-ticket-count" id="countTicket">2</div>
        <div class="btn tp-ticket-add" id="addTicket" @click.prevent="ticketCount++">
          <ion-icon name="add"></ion-icon>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: "Participation",
  data: () => ({
    ticketCount: 2,
  }),
  async mounted() {
    document.title = 'Принять участие';

    Telegram.WebApp.BackButton.show();
    Telegram.WebApp.BackButton.onClick(() => this.$router.push('/'));

    // await this.$store.dispatch('getUser', Telegram.WebApp.initDataUnsafe.user.id)

    let buy_btn;
    if (this.$store.state.lang === 'ru') {
      buy_btn = 'Участвовать'
    } else {
      buy_btn = 'Qatnashish'
    }
    Telegram.WebApp.MainButton.show();
    Telegram.WebApp.MainButton.text = buy_btn;
    Telegram.WebApp.MainButton.textColor = "#FFF";
    Telegram.WebApp.MainButton.color = "#f2c058";
    Telegram.WebApp.MainButton.setParams({"color": "#f2c058"});
    Telegram.WebApp.MainButton.onClick(() => this.buyTicket());

  },
  methods: {
    async buyTicket()  {
      let description;
      if (this.$store.state.lang === 'ru') {
        description = 'тикеты, купить чтобы увеличивающие шансы на выигрыш'
      } else {
        description = 'ticketlar, imkoniyatni oshirish uchun sotib olish'
      }
      await this.$store.dispatch("createInvoice", {
        'payload': this.ticketCount,
        'description': description
      })
    },
  },
  watch: {
    ticketCount() {
      document.getElementById('countTicket').innerText = this.ticketCount
    }
  }
}
</script>

